import React, { useState, useEffect } from 'react';
import { useTheme } from '@mui/material/styles';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
// material
import {
  Menu,
  MenuItem,
  Container,
  IconButton,
  CardHeader,
  CardContent,
  Typography,
  Card,
  Grid,
  Box
} from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';

// components
import Page from '../components/Page';
import useLocalStorge from '../hooks/useLocalStorge';
import { useAuthContext } from '../context/UserProvider';
//

export default function ProjectEditor({ project }) {
  const [anchorEl, setAnchorEl] = useState(null);
  const { getAllProjects } = useAuthContext();
  const [projectDetails, setProjectDetails] = useState(null);
  const [tasks, setTasks] = useLocalStorge('tasks', []);
  const [tasksListByStatus, setTasksListByStatus] = useState([]);
  const [statuses, setStatuses] = useState(['פעיל', 'מושהה', 'בוצע', 'בוטל']);
  const [error, setError] = useState('');
  const open = Boolean(anchorEl);
  const projectId = project;
  const theme = useTheme();
  function handleTaskClick(event) {
    setAnchorEl(event.currentTarget);
  }
  function handleTaskClose() {
    setAnchorEl(null);
  }
  function sortTaskByStatus(tasks) {
    if (!tasks) return null;
    const tasksByStatus = tasks.reduce((acc, task) => {
      if (!acc[task.taskStatus]) {
        acc[task.taskStatus] = [];
      }
      acc[task.taskStatus].push(task);
      return acc;
    }, {});
    if (tasksByStatus[undefined]) {
      Object.defineProperty(
        tasksByStatus,
        'ללא סטטוס',
        Object.getOwnPropertyDescriptor(tasksByStatus, undefined)
      );
      delete tasksByStatus[undefined];
    }
    return tasksByStatus;
  }
  async function getProjects() {
    try {
      setError('');
      const projects = await getAllProjects();
      const project = projects.find((p) => p._id === projectId);
      if (!project) {
        setError('לא נמצא פרויקט');
      } else {
        setProjectDetails(project);
        setTasks(project.tasks);
        setTasksListByStatus(sortTaskByStatus(project.tasks));
      }
    } catch (error) {
      console.log(error);
      setError('לא נמצא פרויקט');
    }
  }
  function handleOnDragEnd(result) {
    if (!result.destination) return;
    const items = Array.from(tasks);
    const [removed] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, removed);
    setTasks(items);
  }
  useEffect(() => {
    getProjects();
  }, []);

  function RenderMultipleDragList({ tasksListByStatus }) {
    if (!tasksListByStatus) return null;
    return (
      <Grid container spacing={2}>
        {Object.keys(tasksListByStatus).map((status, index) => {
          const tasks = tasksListByStatus[status];
          return (
            <Grid item xs={12} sm={6} md={4} key={status}>
              <Typography variant="h6">{`${status} (${tasks.length})`}</Typography>
              <DragDropContext onDragEnd={(res) => handleOnDragEnd(res)}>
                <Droppable droppableId="tasks">
                  {(provided) => (
                    <ul className="tasks" {...provided.droppableProps} ref={provided.innerRef}>
                      {tasks?.map((task, i) => (
                        <Draggable key={task._id} draggableId={task._id} index={i}>
                          {(provided) => (
                            <Card
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                              sx={{
                                margin: '1rem'
                              }}
                            >
                              <CardHeader
                                title={task?.taskName}
                                subheader={task?.taskCreator}
                                action={
                                  <IconButton
                                    aria-label="settings"
                                    aria-controls={`menu-task-${task._id}`}
                                    aria-haspopup="true"
                                    aria-expanded={open}
                                    onClick={(e) => handleTaskClick(e)}
                                  >
                                    <MoreVertIcon />
                                    <Menu
                                      id={`menu-task-${task._id}`}
                                      anchorEl={anchorEl}
                                      open={open}
                                      onClose={() => handleTaskClose()}
                                      MenuListProps={{
                                        'aria-labelledby': 'basic-button'
                                      }}
                                    >
                                      <MenuItem onClick={() => handleTaskClose()}>Profile</MenuItem>
                                      <MenuItem onClick={() => handleTaskClose()}>
                                        My account
                                      </MenuItem>
                                      <MenuItem onClick={() => handleTaskClose()}>Logout</MenuItem>
                                    </Menu>
                                  </IconButton>
                                }
                              />
                              <CardContent>
                                <Typography variant="body2" color="textSecondary" component="p">
                                  {task.taskCreatorDetails?.email || null}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                  cddcdcdc
                                </Typography>
                              </CardContent>
                            </Card>
                          )}
                        </Draggable>
                      ))}
                      {provided.placeholder}
                    </ul>
                  )}
                </Droppable>
              </DragDropContext>
            </Grid>
          );
        })}
      </Grid>
    );
  }

  return (
    <Page title="#כולא-לייק | לוח ניהול: עורך פרוייקט">
      <Container>
        <Typography variant="h6" gutterBottom>
          {!error && projectDetails ? projectDetails.title : error}
        </Typography>
        {projectDetails && (
          <Box direction="row" alignItems="center">
            <Typography variant="subtitle2" gutterBottom>
              {projectDetails.description}
            </Typography>
            <Box ml={2}>
              <Typography variant="h6" gutterBottom>
                {projectDetails.tasks.length} משימות
              </Typography>
              <RenderMultipleDragList tasksListByStatus={tasksListByStatus} />
            </Box>
          </Box>
        )}
      </Container>
    </Page>
  );
}
