import axios from 'axios';

function createUrl() {
  if (process.env.NODE_ENV === 'production') {
    const urlList = ['kulalike.cf', 'kulalike.herokuapp.com'];
    const url = urlList.find((url) => window.location.host.startsWith(url));
    return `https://${url}/api`;
  }
  return 'http://localhost:4000/api';
}

const url = createUrl();
console.log(url);
const api = axios.create({
  baseURL: url
});

export default api;
