import React, { useState, useEffect, useCallback } from 'react';
import * as Yup from 'yup';
import { Icon } from '@iconify/react';
import folderAddOutline from '@iconify/icons-eva/folder-add-outline';
import { useTheme } from '@mui/material/styles';
// material
import {
  TextField,
  Container,
  Stack,
  Typography,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  StepLabel,
  Box,
  Stepper,
  Step,
  StepContent,
  Paper
} from '@mui/material';
// Link as RouterLink,
import { useNavigate } from 'react-router-dom';
import { useFormik, Form, FormikProvider } from 'formik';
// components
import Page from '../components/Page';
import {
  ProductSort,
  ProjectsList,
  AddProjectWidget,
  ProductFilterSidebar,
  TaskCreator
} from '../components/_dashboard/projects';
//
import { useAuthContext } from '../context/UserProvider';

function BasicSetting({ formik, getFieldProps, touched, errors, values }) {
  return (
    <>
      <FormikProvider value={formik}>
        <Form autoComplete="off">
          <Stack spacing={3}>
            <TextField
              fullWidth
              type="text"
              label="שם הפרוייקט"
              {...getFieldProps('title')}
              value={values.title}
              error={Boolean(touched.title && errors.title)}
              helperText={touched.title && errors.title}
            />

            <TextField
              id="filled-multiline-flexible"
              label="תיאור הפרוייקט"
              multiline
              minRows={3}
              maxRows={10}
              value={values.description}
              {...getFieldProps('description')}
              error={Boolean(touched.description && errors.description)}
              helperText={touched.description && errors.description}
            />
          </Stack>
        </Form>
      </FormikProvider>
    </>
  );
}

const headCells = [
  {
    id: 'taskName',
    disablePadding: true,
    label: 'שם המשימה'
  },
  {
    id: 'taskDescription',
    disablePadding: false,
    label: 'תיאור'
  },
  {
    id: 'taskStatus',
    disablePadding: false,
    label: 'סטטוס'
  },
  {
    id: 'taskCreatedAt',
    disablePadding: false,
    label: 'תאריך יצירה'
  },
  {
    id: 'taskCreator',
    disablePadding: false,
    label: 'יוצר משימה'
  }
];

export default function ProjectsPage() {
  const { newProject, setNewProject, sendProject, user } = useAuthContext();

  const NewProjectSchema = Yup.object().shape({
    title: Yup.string()
      .min(3, 'קצר מידיי!')
      .max(50, 'ארוך מידיי!')
      .required('חובה להזין שם לפרוייקט'),
    description: Yup.string()
      .min(10, 'קצר מידיי!')
      .max(200, 'ארוך מידיי!')
      .required('חובה להזין תיאור לפרוייקט')
  });
  const formik = useFormik({
    initialValues: {
      title: newProject.title || '',
      description: newProject.description || ''
    },
    validationSchema: NewProjectSchema,
    onSubmit: () => {
      navigate('/dashboard', { replace: true });
      setOpenFilter(false);
    }
  });
  const { errors, touched, values, isSubmitting, handleSubmit, resetForm, getFieldProps } = formik;
  const theme = useTheme();
  const navigate = useNavigate();
  const [openFilter, setOpenFilter] = useState(false);
  const [open, setOpen] = useState(false);
  const [activeStep, setActiveStep] = useState(0);
  const [rowsData, setRowsData] = useState(newProject.tasks);
  function saveProjectDetails() {
    setNewProject((newProject) => ({
      ...newProject,
      author: user.displayName,
      createdAt: Date.now().toString(),
      updatedAt: '',
      title: values.title,
      description: values.description,
      id: `${new Date().getTime()}_${user.displayName}`,
      tasks: newProject.tasks || []
    }));
  }
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    saveProjectDetails();
  };
  const handleBack = () => setActiveStep((prevActiveStep) => prevActiveStep - 1);
  const handleReset = () => setActiveStep(0);
  const handleClickOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const handleOpenFilter = () => setOpenFilter(true);
  const handleCloseFilter = () => setOpenFilter(false);
  const handleResetFilter = () => {
    handleSubmit();
    resetForm();
  };

  const steps = [
    {
      label: 'הגדרות בסיסיות',
      description: `בשלב הזה ניתן להגדיר את הפרטים הבסיסיים של הפרוייקט. רצוי להזין את כל הפרטים.`,
      component: (
        <BasicSetting
          formik={formik}
          getFieldProps={getFieldProps}
          touched={touched}
          errors={errors}
          values={values}
        />
      )
    },
    {
      label: 'יצירת משימות ושיוך אנשים',
      description:
        'בשלב הזה ניתן להגדיר משימות ולשייך משימות לאנשים. תמיד ניתן להוסיף משימות בהמשך.',
      component: <TaskCreator headCells={headCells} rows={newProject.tasks} />
    }
  ];

  const saveAndSendProject = () => {
    if (newProject && newProject.tasks.length > 0) {
      handleClose();
      sendProject(newProject);
      navigate('/dashboard/projects', { replace: true });
    }
  };
  return (
    <Page title="#כולא-לייק | לוח ניהול: פרוייקטים">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            פרוייקטים
          </Typography>
          <Stack direction="row-reverse" alignItems="center" justifyContent="center">
            <Button
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-evenly'
              }}
              variant="contained"
              onClick={handleClickOpen}
              endIcon={<Icon icon={folderAddOutline} style={{ marginRight: '1rem' }} />}
            >
              הוספת פרוייקט
            </Button>
          </Stack>
        </Stack>
        <Dialog maxWidth="xl" open={open} onClose={handleClose}>
          <DialogTitle>יצירת פרוייקט חדש</DialogTitle>
          <DialogContent>
            {/* <DialogContentText>
              To subscribe to this website, please enter your email address here. We will send
              updates occasionally.
            </DialogContentText> */}
            {/* <ContentStyle> */}
            <Stepper activeStep={activeStep} orientation="vertical">
              {steps.map((step, index) => (
                <Step key={step.label}>
                  <StepLabel
                    optional={
                      index === 1 ? <Typography variant="caption">צעד אחרון</Typography> : null
                    }
                  >
                    {step.label}
                  </StepLabel>
                  <StepContent>
                    <Typography color={theme.palette.text.secondary}>{step.description}</Typography>
                    {step.component && <Box sx={{ width: '100%' }}>{step.component}</Box>}
                    <Box
                      sx={{
                        mb: 2,
                        display: 'flex',
                        direction: 'row',
                        justifyContent: 'left'
                      }}
                    >
                      <Stack direction="row-reverse">
                        <Button
                          disabled={Object.values(errors).some((error) => error)}
                          variant="outlined"
                          onClick={handleNext}
                          sx={{ mt: 1, mr: 1, color: theme.palette.success }}
                        >
                          {index === steps.length - 1 ? 'סיום' : 'קדימה'}
                        </Button>
                        <Button disabled={index === 0} onClick={handleBack} sx={{ mt: 1, mr: 1 }}>
                          חזרה
                        </Button>
                      </Stack>
                    </Box>
                  </StepContent>
                </Step>
              ))}
            </Stepper>
            {activeStep === steps.length && (
              <Paper square elevation={0} sx={{ width: '100%' }}>
                <Typography>מעולה, סיימת את כל הצעדים. הפרוייקט מוכן!</Typography>
                <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
                  איפוס
                </Button>
              </Paper>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>ביטול</Button>
            <Button
              onClick={saveAndSendProject}
              disabled={newProject.tasks === 0 || activeStep !== steps.length}
            >
              שמירת פרוייקט
            </Button>
          </DialogActions>
        </Dialog>
        <Stack
          direction="row"
          flexWrap="wrap-reverse"
          alignItems="center"
          justifyContent="flex-end"
          maxWidth="xl"
          sx={{ mb: 5 }}
        >
          <Stack direction="row" spacing={1} flexShrink={0} sx={{ my: 1 }}>
            <ProductFilterSidebar
              formik={formik}
              isOpenFilter={openFilter}
              onResetFilter={handleResetFilter}
              onOpenFilter={handleOpenFilter}
              onCloseFilter={handleCloseFilter}
            />
            <ProductSort />
          </Stack>
        </Stack>

        <ProjectsList />
      </Container>
    </Page>
  );
}
