import * as React from 'react';
import PropTypes from 'prop-types';
import { alpha } from '@mui/material/styles';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import DeleteIcon from '@mui/icons-material/Delete';
import AddTaskRoundedIcon from '@mui/icons-material/AddTaskRounded';

export default function TaskToolbar(props) {
  const { numSelected, handleClickOpen } = props;
  return (
    <Toolbar
      sx={{
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(numSelected > 0 && {
          bgcolor: (theme) =>
            alpha(theme.palette.primary.main, theme.palette.action.activatedOpacity)
        })
      }}
    >
      {numSelected > 0 ? (
        <Typography sx={{ flex: '1 1 100%' }} color="inherit" variant="subtitle1" component="div">
          {numSelected === 1 ? `משימה ${numSelected} נבחרה` : `${numSelected} משימות נבחרו`}
        </Typography>
      ) : (
        <Typography sx={{ flex: '1 1 100%' }} variant="h6" id="tableTitle" component="div">
          משימות
        </Typography>
      )}

      {numSelected > 0 ? (
        <Tooltip title={numSelected === 1 ? `מחיקת משימה` : `מחיקת ${numSelected} משימות`}>
          <IconButton>
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      ) : (
        <Tooltip title="יצירת משימה">
          <Button
            onClick={handleClickOpen}
            sx={{
              display: 'flex',
              justifyContent: 'space-evenly'
            }}
            fullWidth
            endIcon={<AddTaskRoundedIcon />}
          >
            הוספת משימה חדשה
          </Button>
        </Tooltip>
      )}
    </Toolbar>
  );
}

TaskToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired
};
