import { filter } from 'lodash';
import { useEffect, useState } from 'react';
// material
import {
  Card,
  Table,
  Stack,
  Avatar,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Typography,
  TableContainer,
  TablePagination
} from '@mui/material';
// components
import { useNavigate } from 'react-router-dom';
import Label from './Label';
import Scrollbar from './Scrollbar';
import SearchNotFound from './SearchNotFound';
import { UserListHead, UserListToolbar, UserMoreMenu } from './_dashboard/user';
//
import { useAuthContext } from '../context/UserProvider';
import useLocalStorge from '../hooks/useLocalStorge';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'שם', alignRight: true },
  { id: 'email', label: 'דואר אלטרוני', alignRight: true },
  { id: 'company', label: 'הרשאה', alignRight: true },
  { id: 'role', label: 'מקצוע', alignRight: true },
  // { id: 'isVerified', label: 'מצב אימות', alignRight: true },
  // { id: 'status', label: 'סטטוס', alignRight: true },
  { id: '' }
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_user) => _user.name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function UsersCard({ handleAddUsers }) {
  const { getAllUsers } = useAuthContext();
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('שם');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(3);
  const [users, setUsers] = useLocalStorge('users', []);
  async function getAllUsersData() {
    const _users = await getAllUsers();
    const users = _users?.map((user) => ({
      uid: user.uid,
      name: user.displayName,
      isVerified: user.emailVerified,
      avatarUrl: user.photoURL,
      email: user.email
    }));
    setUsers(users);
  }

  useEffect(() => {
    getAllUsersData();
  }, []);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = users.map((n) => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - users.length) : 0;

  const filteredUsers = applySortFilter(users, getComparator(order, orderBy), filterName);
  const isUserNotFound = filteredUsers?.length === 0;

  return (
    <Card sx={{ width: '100%' }}>
      <UserListToolbar
        numSelected={selected.length}
        filterName={filterName}
        onFilterName={handleFilterByName}
        addUsers={() => handleAddUsers(selected)}
      />

      <Scrollbar>
        <TableContainer sx={{ minWidth: 800 }}>
          <Table>
            <UserListHead
              order={order}
              orderBy={orderBy}
              headLabel={TABLE_HEAD}
              rowCount={users?.length}
              numSelected={selected.length}
              onRequestSort={handleRequestSort}
              onSelectAllClick={handleSelectAllClick}
            />

            <TableBody>
              {filteredUsers
                ?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((rowUserDetails) => {
                  if (!rowUserDetails) return;
                  const { uid, name, isVerified, avatarUrl, email } = rowUserDetails;
                  const isItemSelected = selected.indexOf(name) !== -1;

                  return (
                    <TableRow
                      hover
                      key={uid}
                      tabIndex={-1}
                      role="checkbox"
                      selected={isItemSelected}
                      aria-checked={isItemSelected}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={isItemSelected}
                          onChange={(event) => handleClick(event, name)}
                        />
                      </TableCell>
                      <TableCell component="th" scope="row" padding="none">
                        <Stack
                          direction="row"
                          alignItems="center"
                          spacing={2}
                          justifyContent="right"
                        >
                          <div style={{ marginLeft: '1rem' }}>
                            <Avatar alt={name} src={avatarUrl} />
                          </div>
                          <Typography variant="subtitle2" noWrap>
                            {name}
                          </Typography>
                        </Stack>
                      </TableCell>
                      <TableCell align="right">{email}</TableCell>
                      <TableCell align="right">{isVerified ? 'כן' : 'לא'}</TableCell>
                      <TableCell align="right">
                        <Label
                          variant="ghost"
                          color={(isVerified === 'banned' && 'error') || 'success'}
                        >
                          {isVerified}
                        </Label>
                      </TableCell>

                      <TableCell align="right">
                        <UserMoreMenu />
                      </TableCell>
                    </TableRow>
                  );
                })}
              {emptyRows > 0 && (
                <TableRow style={{ height: 53 * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>

            {isUserNotFound && (
              <TableBody>
                <TableRow>
                  <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                    <SearchNotFound searchQuery={filterName} />
                  </TableCell>
                </TableRow>
              </TableBody>
            )}
          </Table>
        </TableContainer>
      </Scrollbar>

      <TablePagination
        sx={{ display: 'flex', flexDirection: 'row-reverse' }}
        rowsPerPageOptions={[1, 3, 5, 10, { value: -1, label: 'כל הים' }]}
        component="div"
        count={users?.length || 0}
        rowsPerPage={rowsPerPage || 0}
        labelRowsPerPage="שורות לדף:"
        labelDisplayedRows={({ from, to, count }) =>
          `${from || '-'}-${to || '-'} מתוך ${count !== -1 ? count : `יותר מ ${to}`}`
        }
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Card>
  );
}
