import PropTypes from 'prop-types';
import React, { useEffect, useState, useCallback } from 'react';
// material
import { Grid, CircularProgress, Box } from '@mui/material';
import ProjectCard from './ProjectCard';
import { useAuthContext } from '../../../context/UserProvider';
import useLocalStorge from '../../../hooks/useLocalStorge';
// ----------------------------------------------------------------------

export default function ProjectsList({ ...other }) {
  const { getAllProjects } = useAuthContext();
  const [error, setError] = useState('');
  const [projects, setProjects] = useLocalStorge('projects', []);
  const [loading, setLoading] = useState(false);

  useEffect(async () => {
    try {
      setError('');
      setLoading(true);
      const allProjects = await getAllProjects();
      setProjects(allProjects);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setError('ישנה בעיה בטעינת הפרויקטים');
      setLoading(false);
    }
  }, []);

  return (
    <Grid container spacing={1} {...other}>
      {error && <Box>{error}</Box>}
      {!loading ? (
        projects?.map((project) => (
          <Grid key={project.id} item xs={12} sm={6} md={4}>
            <ProjectCard project={project} />
          </Grid>
        ))
      ) : (
        <Grid item xs={12}>
          <Box display="flex" justifyContent="center" alignItems="center" height="100%">
            <CircularProgress />
          </Box>
        </Grid>
      )}
    </Grid>
  );
}
