import React, { forwardRef, useState } from 'react';
import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import searchFill from '@iconify/icons-eva/search-fill';
import trash2Fill from '@iconify/icons-eva/trash-2-fill';
import roundFilterList from '@iconify/icons-ic/round-filter-list';

// material
import { styled } from '@mui/material/styles';
import {
  Box,
  Toolbar,
  Tooltip,
  IconButton,
  Typography,
  OutlinedInput,
  InputAdornment,
  Stack,
  Button,
  Snackbar,
  Slide
} from '@mui/material';

// ----------------------------------------------------------------------

const RootStyle = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3)
}));

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  width: 240,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': { width: 320, boxShadow: theme.customShadows.z8 },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${theme.palette.grey[500_32]} !important`
  }
}));

// ----------------------------------------------------------------------

UserListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func
};

export default function UserListToolbar({ numSelected, filterName, onFilterName, addUsers }) {
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(true);
    addUsers();
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  return (
    <RootStyle
      sx={{
        ...(numSelected > 0 && {
          color: 'primary.main',
          bgcolor: 'primary.lighter'
        })
      }}
    >
      {numSelected > 0 ? (
        <Typography component="div" variant="subtitle1">
          {numSelected === 1 ? `משתמש ${numSelected} נבחר.` : `${numSelected} משתמשים נבחרו`}
        </Typography>
      ) : (
        <SearchStyle
          value={filterName}
          onChange={onFilterName}
          placeholder="חיפוש משתמשים..."
          startAdornment={
            <InputAdornment position="start">
              <Box component={Icon} icon={searchFill} sx={{ color: 'text.disabled' }} />
            </InputAdornment>
          }
        />
      )}

      {numSelected > 0 ? (
        <Tooltip title="שיוך">
          <Stack direction="row-reverse" alignItems="center" justifyContent="center">
            <Button
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-evenly'
              }}
              variant="contained"
              onClick={handleClick}
              endIcon={<Icon icon="add" style={{ marginRight: '1rem' }} />}
            >
              שיוך / בחירה
            </Button>
          </Stack>
        </Tooltip>
      ) : (
        <Tooltip title="Filter list">
          <IconButton>
            <Icon icon={roundFilterList} />
          </IconButton>
        </Tooltip>
      )}
      <Snackbar
        open={open}
        autoHideDuration={1000}
        onClose={handleClose}
        TransitionComponent={(props) => <Slide {...props} direction="up" />}
        message={
          numSelected === 1
            ? `משתמש ${numSelected} שויך בהצלחה.`
            : `${numSelected} משתמשים שויכו בהצלחה`
        }
      />
    </RootStyle>
  );
}
