import * as Yup from 'yup';
import { useState, useEffect } from 'react';
import { useFormik, Form, FormikProvider } from 'formik';
// material
import { Stack, TextField, MenuItem, Box, Typography } from '@mui/material';
import UsersCard from '../../UsersCard';
import { useAuthContext } from '../../../context/UserProvider';
// ----------------------------------------------------------------------
const taskStatus = [
  {
    value: 'פעיל',
    color: 'green'
  },
  {
    value: 'מושהה',
    color: 'red'
  },
  {
    value: 'בוצע',
    color: 'blue'
  },
  {
    value: 'בוטל',
    color: 'black'
  }
];

export default function TaskForm() {
  const { user, newProject, setNewProject } = useAuthContext();
  const TaskSchema = Yup.object().shape({
    taskName: Yup.string()
      .min(2, 'קצר מידיי!')
      .max(50, 'ארוך מידיי!')
      .required('חובה להזין שם משימה'),
    tastDescription: Yup.string().required('חובה להזין תיאור משימה'),
    taskStatus: Yup.string().required('חובה להזין סטטוס משימה')
  });
  const [statuses, setStatuses] = useState('פעיל');

  const handleChange = (e) => setStatuses(e.target.value);
  const formik = useFormik({
    initialValues: {
      taskName: '',
      tastDescription: '',
      taskStatus: statuses,
      createDate: '',
      taskCreator: '',
      taskCreatorDetails: {
        fullName: '',
        email: '',
        moreDetails: {
          phone: '',
          linkedin: ''
        }
      },
      taskAssociates: []
    },
    validationSchema: TaskSchema
  });

  const { errors, touched, getFieldProps, values } = formik;

  const handleAddUsers = (selected) => {
    const newTask = {
      taskName: values.taskName,
      tastDescription: values.tastDescription,
      taskStatus: statuses,
      createDate: new Date().toLocaleDateString(),
      taskCreator: user.displayName,
      taskCreatorDetails: {
        fullName: user.displayName,
        email: user.email,
        moreDetails: {
          phone: user.phone,
          linkedin: user.linkedin
        }
      },
      taskAssociates: selected
    };
    setNewProject({
      ...newProject,
      tasks: [...newProject.tasks, newTask]
    });
  };

  return (
    <FormikProvider value={formik}>
      <Form autoComplete="off" noValidate>
        <Stack spacing={3}>
          <Stack justifyContent="space-between" direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <TextField
              fullWidth
              label="שם המשימה"
              {...getFieldProps('taskName')}
              error={Boolean(touched.taskName && errors.taskName)}
              helperText={touched.taskName && errors.taskName}
              onChange={formik.handleChange}
            />
          </Stack>
          <TextField
            fullWidth
            rows={4}
            multiline
            label="תיאור"
            {...getFieldProps('tastDescription')}
            error={Boolean(touched.tastDescription && errors.tastDescription)}
            helperText={touched.tastDescription && errors.tastDescription}
          />
          <TextField
            id="statuses"
            select
            label="סטטוס המשימה"
            value={statuses}
            onChange={handleChange}
            // helperText="סטטוס המשימה. (ברירת המחדל: פעיל)"
          >
            {taskStatus.map(({ value, color }) => (
              <MenuItem key={value} value={value}>
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                    justifyContent: 'space-between',
                    padding: '0 0.25rem',
                    alignItems: 'center'
                  }}
                >
                  <span>{value}</span>
                  <span
                    style={{
                      backgroundColor: color,
                      width: '10px',
                      height: '10px',
                      borderRadius: '50%'
                    }}
                  />
                </div>
              </MenuItem>
            ))}
          </TextField>

          <Stack spacing={2}>
            <Box>
              <Typography variant="subtitle1">שיוך משתמשים</Typography>
            </Box>
            <UsersCard handleAddUsers={handleAddUsers} />
          </Stack>
        </Stack>
      </Form>
    </FormikProvider>
  );
}
