import { Icon } from '@iconify/react';
import folderAddOutline from '@iconify/icons-eva/folder-add-outline';
import { styled } from '@mui/material/styles';
import { Badge, Link } from '@mui/material';
import { Link as RouterLink, useLocation } from 'react-router-dom';

// ----------------------------------------------------------------------

const RootStyle = styled('div')(({ theme }) => ({
  zIndex: 999,
  right: 0,
  display: 'flex',
  cursor: 'pointer',
  position: 'fixed',
  alignItems: 'center',
  top: theme.spacing(16),
  height: theme.spacing(5),
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  paddingTop: theme.spacing(1.25),
  boxShadow: theme.customShadows.z20,
  color: theme.palette.text.primary,
  backgroundColor: theme.palette.background.paper,
  borderTopLeftRadius: theme.shape.borderRadiusMd,
  borderBottomLeftRadius: theme.shape.borderRadiusMd,
  transition: theme.transitions.create('opacity'),
  '&:hover': { opacity: 0.72 }
}));

// ----------------------------------------------------------------------

export default function AddProjectWidget() {
  const location = useLocation();
  return (
    <RootStyle>
      <Link underline="none" variant="subtitle2" component={RouterLink} to="/dashboard/new_project">
        <Badge showZero color="error" max={99}>
          <Icon icon={folderAddOutline} width={24} height={24} />
        </Badge>
      </Link>
    </RootStyle>
  );
}
