export default function hendleError(err) {
  if (err.code === 'auth/account-exists-with-different-credential') {
    return 'חשבון עם דואר אלקטרוני זהה כבר קיים במערכת';
  }
  if (err.code === 'auth/popup-closed-by-user') {
    return 'הפעלת החשבון בוטלה על ידי המשתמש';
  }
  if (err.code === 'auth/operation-not-allowed') {
    return 'הפעלת החשבון בוטלה על ידי המערכת';
  }
  if (err.code === 'auth/popup-blocked') {
    return 'הפעלת החשבון בוטלה על ידי המערכת';
  }
  return 'אירעה שגיאה בעת התחברות למערכת';
}
