import React, { createContext, useContext, useEffect, useState } from 'react';

import {
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
  FacebookAuthProvider,
  signOut
} from 'firebase/auth';

import firebaseApp from '../auth/firebase';
import api from '../api';
import hendleError from '../controllers/errors';
import useLocalStorge from '../hooks/useLocalStorge';
// ------------------------------------------------
const Context = createContext(null);
const { Provider } = Context;
const initialProjectValue = {
  id: '',
  title: '',
  description: '',
  author: '',
  createdAt: '',
  updatedAt: '',
  tasks: []
};
function AuthContext() {
  const auth = getAuth(firebaseApp);
  const googleProvider = new GoogleAuthProvider().addScope('email', 'profile', 'gander');
  const facebookProvider = new FacebookAuthProvider().addScope('email', 'public_profile');
  const [user, setUser] = useLocalStorge('user', '');
  const [token, setToken] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [newProject, setNewProject] = useLocalStorge('newProject', initialProjectValue);
  const [isAuthWith, setIsAuthWith] = useState({
    google: false,
    facebook: false
  });
  const [error, setError] = useState(null);

  async function login(provider) {
    try {
      setError(null);
      setIsLoading(true);
      setIsAuthWith({
        google: provider.providerId.toLowerCase() === 'google.com',
        facebook: provider.providerId.toLowerCase() === 'facebook.com'
      });
      setUser(null);
      setToken(null);
      const result = await signInWithPopup(auth, provider);
      const { user } = result;
      const jwt = await user.getIdToken();
      const { data } = await api.post('/auth/login', { jwt });
      console.log(data);
      if (data) {
        setError(null);
        setUser(user);
        setToken(jwt);
        setIsLoading(false);
        setIsAuthWith({
          google: user.providerData.find((provider) => provider.providerId === 'google.com'),
          facebook: user.providerData.find((provider) => provider.providerId === 'facebook.com')
        });
      }
    } catch (error) {
      const currentError = hendleError(error);
      setError(currentError);
      setIsLoading(false);
      setIsAuthWith({
        google: false,
        facebook: false
      });
      setUser(null);
      setToken(null);
      console.log(error);
    }
  }
  async function logout() {
    try {
      setError(null);
      setIsLoading(true);
      setIsAuthWith({
        google: false,
        facebook: false
      });
      setUser(null);
      setToken(null);
      await signOut(auth);
      setIsLoading(false);
    } catch (error) {
      const currentError = hendleError(error);
      setError(currentError);
      setIsLoading(false);
      setIsAuthWith({
        google: false,
        facebook: false
      });
      setUser(null);
      setToken(null);
      console.log(error);
    }
  }
  async function loginWithGoogle() {
    await login(googleProvider);
  }
  async function loginWithFacebook() {
    await login(facebookProvider);
  }
  async function getAllUsers() {
    try {
      const { data } = await api.get('/users', {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      const { users } = data;
      return users;
    } catch (error) {
      return error;
    }
  }

  async function getAllProjects() {
    try {
      const { data } = await api.get('/projects');
      const { projects } = data;
      return projects;
    } catch (error) {
      console.log(error);
    }
  }
  async function sendProject(project) {
    console.log(project);
    try {
      const { data } = await api.post('projects/newProject', { project });
      if (data === 'פרוייקט נשמר בהצלחה') {
        console.log(data);
        setNewProject(initialProjectValue);
      }
      return;
    } catch (error) {
      return error;
    }
  }
  return {
    user,
    token,
    isLoading,
    isAuthWith,
    error,
    newProject,
    setNewProject,
    sendProject,
    getAllProjects,
    logout,
    loginWithGoogle,
    loginWithFacebook,
    getAllUsers
  };
}

export default function AuthProvider({ children }) {
  const globalContext = AuthContext();
  return <Provider value={globalContext}>{children}</Provider>;
}

AuthProvider.context = Context;

export const useAuthContext = () => useContext(Context);

