import { useEffect, useState } from 'react';
import { useTheme } from '@mui/material/styles';
import {
  Box,
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Paper,
  Checkbox,
  Button,
  FormControlLabel,
  Switch,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  useMediaQuery
} from '@mui/material';

import {
  TaskTableHead,
  TaskToolbar,
  createData,
  descendingComparator,
  getComparator,
  stableSort
} from '.';

import TaskForm from './TaskForm';
import { useAuthContext } from '../../../context/UserProvider';
// -------------------
export default function TaskCreator({ headCells, rows }) {
  const { newProject, setNewProject } = useAuthContext();
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('taskName');
  const [selected, setSelected] = useState([]);
  const [page, setPage] = useState(0);
  const [dense, setDense] = useState(false);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = rows.map((n) => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangeDense = (event) => {
    setDense(event.target.checked);
  };

  const isSelected = (name) => selected.indexOf(name) !== -1;

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;
  const [open, setOpen] = useState(false);
  const [taskDetails, setTaskDetails] = useState({});
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xl'));

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const saveTask = () => {
    setNewProject({ ...newProject, tasks: [...newProject.tasks, taskDetails] });
    setOpen(false);
  };
  return (
    <Container sx={{ width: '100%' }}>
      <TaskToolbar handleClickOpen={handleClickOpen} numSelected={selected.length} />
      <Box sx={{ width: '100%' }}>
        <Dialog
          fullScreen
          open={open}
          onClose={handleClose}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">יצירת משימה חדשה</DialogTitle>
          <DialogContent>
            <Box
              sx={{
                padding: '2rem',
                width: '100%',
                position: 'relative'
              }}
            >
              <TaskForm />
            </Box>
          </DialogContent>
          <DialogActions>
            <Button autoFocus onClick={handleClose}>
              ביטול
            </Button>
            <Button
              onClick={saveTask}
              autoFocus
              disabled={newProject.tasks[newProject.tasks.length - 1]?.taskAssociates?.length < 1}
              color="primary"
            >
              שמירת משימה
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
      {rows.length > 0 ? (
        <div>
          <TableContainer>
            <Table
              sx={{ minWidth: 750 }}
              aria-labelledby="tableTitle"
              size={dense ? 'small' : 'medium'}
            >
              <TaskTableHead
                headCells={headCells}
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={handleSelectAllClick}
                onRequestSort={handleRequestSort}
                rowCount={rows.length}
              />
              <TableBody>
                {stableSort(rows, getComparator(order, orderBy))
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row, index) => {
                    const isItemSelected = isSelected(row.name);
                    const labelId = `enhanced-table-checkbox-${index}`;
                    return (
                      <TableRow
                        hover
                        onClick={(event) => handleClick(event, row.taskName)}
                        role="checkbox"
                        aria-checked={isItemSelected}
                        tabIndex={-1}
                        key={row.taskName}
                        selected={isItemSelected}
                      >
                        <TableCell padding="checkbox">
                          <Checkbox
                            color="primary"
                            checked={isItemSelected}
                            inputProps={{
                              'aria-labelledby': labelId
                            }}
                          />
                        </TableCell>
                        <TableCell
                          align="right"
                          component="th"
                          id={labelId}
                          scope="row"
                          padding="none"
                        >
                          {row.taskName}
                        </TableCell>
                        <TableCell align="right">{row.tastDescription}</TableCell>
                        <TableCell align="right">{row.taskStatus}</TableCell>
                        <TableCell align="right">{row.createDate}</TableCell>
                        <TableCell align="right">{row.taskCreator}</TableCell>
                      </TableRow>
                    );
                  })}
                {emptyRows > 0 && (
                  <TableRow
                    style={{
                      height: (dense ? 33 : 53) * emptyRows
                    }}
                  >
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>

          <TablePagination
            sx={{
              display: 'flex',
              justifyContent: 'space-between'
            }}
            labelRowsPerPage="תצוגת משימות:"
            rowsPerPageOptions={[1, 3, 5, 10, { value: -1, label: 'כל המשימות' }]}
            component="div"
            count={rows.length}
            rowsPerPage={rowsPerPage}
            labelDisplayedRows={({ from, to, count }) =>
              `${from}-${to} מתוך ${count !== -1 ? count : `יותר מ ${to}`}`
            }
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </div>
      ) : (
        <div>
          <h4>טבלת המשימות ריקה!</h4>
          <p>לא ניתן ליצור פרוייקט חדש ללא שיוך אף משימה</p>
        </div>
      )}
      {!!rows.length && (
        <FormControlLabel
          control={<Switch checked={dense} onChange={handleChangeDense} />}
          label={dense ? 'רווחים צרים' : 'רווחים רגילים'}
        />
      )}
    </Container>
  );
}
